$red-clr: #131313;
$gray-clr: #666666;

.footer {
	margin-top: 60px;
	background-color: $red-clr;
	padding: 59px 0;
	color: $gray-clr;
	&-policy-link {
		font-size: 12px; }
	p {
		margin-bottom: 14px; }
	a {
		color: $gray-clr;
		text-decoration: none; }
	a:hover {
		color: #fff; }
	small {
		display: block;
		margin-top: 56px;
		line-height: 16px;
		font-size: 10px;
		max-width: 80%; }
	ul {
		padding: 0;
		list-style: none; }
	ul li {
		line-height: 32px; }
	&-contacts, &-social-links {
		text-align: right; }
	&-contacts_phone {
		font: {
			weight: 400;
			size: 20px; }
		color: #009adf; }
	&-social-links li {
		display: inline-block; }
	&-social-links li a {
				display: inline-block;
				width: 42px;
				height: 42px;
				background-color: rgba(255, 255, 255, 0.1);
				text-align: center;
				border-radius: 50%;
				line-height: 42px;
				margin-left: 10px;
				-webkit-transition: all 0.2s;
				transition: all 0.2s; }
	&-social-links li a:hover {
		background-color: rgba(255, 255, 255, 0.9);
		color: $red-clr; } }

